import { DISABLED_OPACITY } from "@/constants"
import { $hover } from "@/theme/utils"
import { $color, $shadow } from "@/theme/utils"
import { menuAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"


const baseStyleList: SystemStyleFunction = (props) => {
  return {
    bg: $color(props, 'bg.normal'),
    boxShadow: $shadow(props, 'normal'),
    color: $color(props, 'text.normal'),
    minW: '12.5rem',
    borderRadius: '2xs',
    border: 'none',
    p: 0,
    fontSize: 'sm',
    overflow: 'hidden',
    zIndex: 1,
  }
}

const baseStyleItem: SystemStyleFunction = (props) => {
  const { colorScheme: c  } = props

  return {
    py: 0,
    px: '2xl',
    h: '5xl',
    _hover: {
      bg: $hover(props),
    },
    _focus: {
      bg: '',
    },
    _disabled: {
      opacity: DISABLED_OPACITY,
      cursor: 'not-allowed',
      _hover: {
        bg: '',
      }
    },
    "> span > svg": {
      color: $color(props, `scene.${c}.normal`),
    },
  }
}

const baseStyleGroupTitle: SystemStyleObject = {
  mx: 0,
  my: 0,
  px: '2xl',
  h: '5xl',
  fontWeight: 600,
  display: 'flex',
  alignItems: 'center',
}

const baseStyleCommand: SystemStyleObject = {
  opacity: 0.6,
}

const baseStyleDivider: SystemStyleFunction = (props) => {
  return {
    borderColor: $color(props, 'scene.border'),
    m: 0,
  }
}

const baseStyleButton: SystemStyleObject = {
  transitionProperty: "common",
  transitionDuration: "normal",
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  button: baseStyleButton,
  list: baseStyleList(props),
  item: baseStyleItem(props),
  groupTitle: baseStyleGroupTitle,
  command: baseStyleCommand,
  divider: baseStyleDivider(props),
})

const defaultProps = {
  colorScheme: '?'
}

export const Menu = {
  parts: parts.keys,
  baseStyle,
  defaultProps,
}