import {
  Stack,
  Button,
} from '@/index'
import { LightbulbIcon, EmailIcon } from '@node-real/bee-icons'

import Example from '../components/Example'
import ExampleItem from '../components/ExampleItem'

const colorSchemeList = ['primary', 'secondary', 'success', 'danger', 'warning', 'purple', 'text']
const variantList = ['solid', 'ghost', 'outline', 'link']

function ExampleCreator(props: any) {
  return (
    <>
      {variantList.map((v, vi) => {
        return (
          <Stack key={vi} spacing={16} direction='row' align='center'>
            {colorSchemeList.map((c: any, ci) => {
              return (
                <Button colorScheme={c} variant={v} key={ci} {...props} as='a'>{v}</Button>
              )
            })}
          </Stack>
        )
      })}
    </>
  )
}


export default function() {
  return (
    <Example title="Button">
      <ExampleItem title="Colors">
        <ExampleCreator />
      </ExampleItem>

      <ExampleItem title="Sizes" spacing={16} direction='row' align='center'>
        <Button size='xs'>
          extra small
        </Button>
        <Button size='sm'>
          small
        </Button>
        <Button size='md'>
          medium
        </Button>
        <Button size='lg'>
          large
        </Button>
      </ExampleItem>

      <ExampleItem title="Variants">
        <ExampleCreator />
      </ExampleItem>

      <ExampleItem title="Icons" direction='row' spacing={16} align='center'>
        <Button leftIcon={<EmailIcon />} variant='solid'>
          Email
        </Button>
        <Button rightIcon={<LightbulbIcon />} variant='outline'>
          Call us
        </Button>
      </ExampleItem>

      <ExampleItem title="Loading">
        <ExampleCreator
          isLoading
          loadingText='Loading'
          spinnerPlacement='start'
        />
      </ExampleItem>

      <ExampleItem title="Disabled">
        <ExampleCreator isDisabled />
      </ExampleItem>
    </Example>
  )
}