import React, { useContext } from "react"
import { StepsItemContext } from './context';
import { Circle, CenterProps, useColor } from '@/index'
import { TickIcon } from '@node-real/bee-icons'
import { ActiveState } from "./ActiveState";
import { useTextColorForSolidBg } from "@/theme/utils/useTextColorForSolidBg";

interface StepsItemProps extends CenterProps {}

const Component = React.forwardRef((props: StepsItemProps, ref: React.Ref<any>) => {
  const { children, ...otherProps } = props

  const { isActive, isCompleted, colorScheme } = useContext(StepsItemContext)

  const activeColor = useColor(`scene.${colorScheme}.normal`)
  const normalColor = useColor('scene.border')
  const textColor = useTextColorForSolidBg(colorScheme)

  const bgColor = (isActive || isCompleted) ? activeColor : normalColor
  const color = (isActive || isCompleted) ? textColor : ''

  return (
    <Circle
      ref={ref}
      size='md'
      bg={bgColor}
      color={color}
      fontSize='xs'
      position='relative'
      { ...otherProps }
    >
      { isActive && <ActiveState bgColor={bgColor} /> }
      { isCompleted && <TickIcon height="0.8em" /> }
      { !isCompleted && children }
    </Circle>
  )
})


export {
  Component as StepsItem,
  type StepsItemProps,
}
