import React from 'react'
import { AlertDialog, type AlertDialogProps } from '@chakra-ui/react'
import { ModalSizeType } from '@/types'
import { type FocusableElement } from '@chakra-ui/utils'

interface Props extends Omit<AlertDialogProps, 'leastDestructiveRef'> {
  size?: ModalSizeType,
  leastDestructiveRef?: React.RefObject<FocusableElement>
}


const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  const cancelRef = React.useRef<any>()

  return (
    <AlertDialog
      scrollBehavior='inside'
      isCentered
      leastDestructiveRef={cancelRef}
      {...props}
    />
  )
})


export {
  Component as AlertDialog,
  type Props as AlertDialogProps,
}