import { TOAST_DURATION } from "@/constants";
import {  useToast, ToastId, RenderProps, UseToastOptions, CloseAllToastsOptions } from "@chakra-ui/react";
import { Toast, ToastProps } from './component'

type ToastProtoType = ReturnType<typeof useToast>

export interface ToastHelper {
  (options?: ToastProps): ToastId
  close: (id: ToastId) => void
  update: (id: ToastId, options: Omit<ToastProps, "id">) => void
  closeAll: (options?: CloseAllToastsOptions) => void
  isActive: (id: ToastId) => boolean
}


export function createToast(toastProto: ToastProtoType) {
  const t = (options: ToastProps = {}) => {

    options.position = options.position ?? 'top-right'
    options.duration = options.duration ?? TOAST_DURATION
    options.containerStyle = {
      margin: 0,
      width: '100%',
      maxWidth: '100%',
      minWidth: 0,
      ...options.containerStyle,
    }

    if (!options.render) {
      options.render = (props: RenderProps) => <Toast { ...props } { ...options } />
    }

    return toastProto(options as UseToastOptions)
  }

  t.close = toastProto.close
  t.closeAll = toastProto.closeAll
  t.update = toastProto.update
  t.isActive = toastProto.isActive

  return t as ToastHelper
}