import React from 'react'
import { Modal, type ModalProps } from '@chakra-ui/react'
import { ModalSizeType } from '@/types'

interface Props extends ModalProps {
  size?: ModalSizeType,
}


const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Modal scrollBehavior='inside' isCentered {...props}></Modal>
  )
})


export {
  Component as Modal,
  type Props as ModalProps,
}