import { PinInput, type PinInputProps } from '@chakra-ui/react' 
import { SizeType, InputVariant, ColorScheme } from '@/types'
import React from 'react'

interface Props extends PinInputProps {
  variant?: InputVariant,
  size?: SizeType,
  colorScheme?: ColorScheme,
}

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <PinInput {...props} />
  )
})

export {
  Component as PinInput,
  type Props as PinInputProps,
}