import {
  ChakraProvider, ChakraProviderProps,
} from '@chakra-ui/react'
import { useCallback, useMemo, useState } from 'react';
import { theme as defaultTheme } from '@/theme'
import React from 'react';
import CssVarUpdater from './CssVarUpdater';
import { extendTheme } from '@/utils/extendTheme'
import { ToastProvider } from '@/components/feedback/Toast';


function getSystemColorMode() {
  const isDark = !!window?.matchMedia("(prefers-color-scheme: dark)")?.matches
  return isDark ? 'dark' : 'light'
}


interface ThemeProviderProps extends ChakraProviderProps {}


const ThemeProvider = (props: ThemeProviderProps) => {
  const { theme = defaultTheme, children, ...otherProps } = props

  const initColorMode = useMemo(() => {
    const { initialColorMode } = theme.config
    const sysColorMode = getSystemColorMode()

    return initialColorMode === 'system' ? sysColorMode : initialColorMode
  }, [theme])

  const [colorMode, setColorMode] = useState(initColorMode)

  const customTheme = useMemo(() => {
    const extTheme = extendTheme(theme)

    const finalTheme = {
      ...extTheme,
      colors: {
        ...extTheme.colors[colorMode],
        ...extTheme.colors,
      },
      shadows: {
        ...extTheme.shadows[colorMode],
        ...extTheme.shadows,
      }
    }

    return finalTheme
  }, [theme, colorMode])

  const onChangeColorMode = useCallback((colorMode: string) => {
    setColorMode(colorMode)
  }, [])

  return (
    <ChakraProvider theme={customTheme} { ...otherProps }>
      <ToastProvider>
        <CssVarUpdater onChangeColorMode={onChangeColorMode}>
          {children}
        </CssVarUpdater>
      </ToastProvider>
    </ChakraProvider>
  )
}


export {
  ThemeProvider,
  type ThemeProviderProps,
}