import {
  Stack,
  Code,
  Heading,
  Checkbox,
} from '@/index'

import React from 'react'

import Example from '../components/Example'
import ExampleItem from '../components/ExampleItem'

export default function() {
  const [checkedItems, setCheckedItems] = React.useState([false, false])

  const allChecked = checkedItems.every(Boolean)
  const isIndeterminate = checkedItems.some(Boolean) && !allChecked

  return (
    <Example title="Checkbox">

      <ExampleItem title="Usage" direction='row'>
        <Checkbox defaultChecked>Checkbox</Checkbox>
      </ExampleItem>

      <ExampleItem title="Disabled" spacing={5} direction='row'>
        <Checkbox isDisabled>Checkbox</Checkbox>
        <Checkbox isDisabled defaultChecked>
          Checkbox
        </Checkbox>
      </ExampleItem>

      <ExampleItem title="Sizes" spacing={5} direction="row">
        <Checkbox size='xs'>
          Checkbox
        </Checkbox>
        <Checkbox size='sm'>
          Checkbox
        </Checkbox>
        <Checkbox size='md' defaultChecked>
          Checkbox
        </Checkbox>
        <Checkbox size='lg' defaultChecked>
          Checkbox
        </Checkbox>
      </ExampleItem>

      <ExampleItem title="Invalid" direction='row'>
        <Checkbox isInvalid>Checkbox</Checkbox>
      </ExampleItem>

      <ExampleItem title="Indeterminate">
        <Checkbox
          isChecked={allChecked}
          isIndeterminate={isIndeterminate}
          onChange={(e) => setCheckedItems([e.target.checked, e.target.checked])}
        >
          Parent Checkbox
        </Checkbox>
        <Stack pl={6} mt={1} spacing={1}>
          <Checkbox
            isChecked={checkedItems[0]}
            onChange={(e) => setCheckedItems([e.target.checked, checkedItems[1]])}
          >
            Child Checkbox 1
          </Checkbox>
          <Checkbox
            isChecked={checkedItems[1]}
            onChange={(e) => setCheckedItems([checkedItems[0], e.target.checked])}
          >
            Child Checkbox 2
          </Checkbox>
        </Stack>
      </ExampleItem>

      <ExampleItem title="Colors" direction='row'>
        <Checkbox defaultChecked colorScheme='primary'>Checkbox</Checkbox>
        <Checkbox defaultChecked colorScheme='secondary'>Checkbox</Checkbox>
        <Checkbox defaultChecked colorScheme='success'>Checkbox</Checkbox>
        <Checkbox defaultChecked colorScheme='danger'>Checkbox</Checkbox>
        <Checkbox defaultChecked colorScheme='warning'>Checkbox</Checkbox>
        <Checkbox defaultChecked colorScheme='purple'>Checkbox</Checkbox>
      </ExampleItem>
    </Example>
  )
}