import { ColorScheme } from "@/types";
import { useColorMode, useTheme } from '@/index'
import { $textColorForSolidBg } from "./index";

export function useTextColorForSolidBg(colorScheme: ColorScheme, isActive = false) {
  const { colorMode } = useColorMode()
  const theme = useTheme()

  const props = {
    colorScheme,
    colorMode,
    theme,
  }

  return $textColorForSolidBg(props, isActive)
}