import { deepCopy } from '@/theme/utils/deepCopy'
import { theme } from '@/theme'

type Dict = Record<string, any>

function merge(first: Dict, second?: Dict): Dict{
  for (let key in second) {
    first[key] = first[key] && first[key].toString() === "[object Object]" ?
    merge(first[key], second[key]) : first[key] = second[key];
  }

  return first;
}


export function extendTheme(userTheme: Record<string, any>) {
  const copyTheme = deepCopy(theme)
  const mergeTheme = merge(copyTheme, userTheme)

  const {
    colors: {
      light,
      dark,
    },
    config: {
      defaultColorScheme,
      useCustomSecondaryColor,
    }
  } = mergeTheme

  if (!light.scene.text) {
    light.scene.text = {
      normal: light.text.normal,
      active: light.text.normal,
    }
  }
  if (!dark.scene.text) {
    dark.scene.text = {
      normal: dark.text.normal,
      active: dark.text.normal,
    }
  }

  if (!useCustomSecondaryColor) {
    light.scene.secondary.active = light.scene[defaultColorScheme].active
    dark.scene.secondary.active = dark.scene[defaultColorScheme].active
  }

  Object.values(mergeTheme.components).forEach((item: any) => {
    if (item?.defaultProps?.colorScheme === '?') {
      Object.defineProperty(item.defaultProps, 'colorScheme', {
        enumerable: true,
        configurable: true,
        get: () => mergeTheme.config.defaultColorScheme
      })
    }
  })

  return mergeTheme
}