import React from "react";
import { Circle, type CenterProps } from '@/index'
import { keyframes } from "@/utils/keyframes";
import { rgba } from "polished";


const animation = (color: string) => 
  keyframes({
    '0%': {
      transform: 'scale(0.95)',
      boxShadow: `0 0 0 0 ${color}`
    },
    
    '70%': {
      transform: 'scale(1)',
      boxShadow: `0 0 0 0.75rem transparent`
    },
    
    '100%': {
      transform: 'scale(0.95)',
      boxShadow: `0 0 0 0 transparent`
    }
  })


interface ActiveStateProps extends CenterProps {
  bgColor: string
}

const Component = React.forwardRef((props: ActiveStateProps, ref: React.Ref<any>) => {
  const color = rgba(props.bgColor, 0.8)

  return (
    <Circle
      ref={ref}
      position='absolute'
      top='0px'
      left='0px'
      width='100%'
      height='100%'
      animation={`${animation(color)} 2s infinite`}
      zIndex={-1}
      {...props}
    />
  )
})


export {
  Component as ActiveState,
  type ActiveStateProps,
}