import { Radio, type RadioProps } from '@chakra-ui/react'
import { ColorScheme, SizeType } from '@/types'
import React from 'react'


interface Props extends RadioProps {
  size?: SizeType,
  colorScheme?: ColorScheme,
}

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Radio ref={ref} {...props} />
  )
})

export {
  Component as Radio,
  type Props as RadioProps,
}