import { DISABLED_OPACITY } from "@/constants"
import { $color, $textColorForSolidBg } from "@/theme/utils"
import {
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"


const baseStyle: SystemStyleFunction = (props) => {
  const { colorScheme: c = 'primary' } = props

  const color = $textColorForSolidBg(props)

  return {
    borderRadius: "0.333333em",
    bg: $color(props, 'bg.input.normal'),
    _disabled: {
      opacity: DISABLED_OPACITY,
      cursor: "not-allowed",
    },
    _hover: {
      bg: $color(props, `scene.${c}.normal`),
      color,
      _disabled: {
        bg: '',
        color: '',
      }
    },
    _active: {
      bg: '',
    },
  }
}

const sizes: Record<string, SystemStyleObject> = {
  lg: {
    w: "5xl",
    h: '5xl',
    fontSize: 'md',
  },
  md: {
    w: "4xl",
    h: '4xl',
    fontSize: "sm",
  },
  sm: {
    w: "2xl",
    h: '2xl',
    fontSize: "0.625rem",
  },
  xs: {
    w: "md",
    h: 'md',
    fontSize: "2xs",
  },
}

const defaultProps = {
  size: "sm",
  colorScheme: '?',
}

export const CloseButton = {
  baseStyle,
  sizes,
  defaultProps,
}