import { Tooltip, Button, useClipboard, TooltipProps, ButtonProps, useColor, IconButton } from "@/index"
import { CopyIcon } from '@node-real/bee-icons'
import React from "react"

interface CopyToClipboardProps extends ButtonProps {
  value: string,
  text?: string,
  tips?: string,
  copiedTips?: string,
  tooltipProps?: TooltipProps,
}

const Component = React.forwardRef((props: CopyToClipboardProps, ref: React.Ref<any>) => {
  const {
    value,
    text,
    tips = 'Copy',
    copiedTips = 'Copied!',
    tooltipProps,
    ...otherProps
  } = props

  const { hasCopied, onCopy } = useClipboard(value)
  const color = useColor('scene.success.normal')

  const mergeTooltipProps = {
    hasArrow: true,
    label: hasCopied ? copiedTips : tips,
    placement: 'top' as any,
    color: hasCopied ? color : 'inherit',
    isOpen: hasCopied ? hasCopied : undefined,
    ...tooltipProps,
  }

  return (
    <Tooltip { ...mergeTooltipProps }>
      { text === undefined ? 
        <IconButton icon={<CopyIcon />} onClick={onCopy} { ...otherProps } /> :
        <Button leftIcon={<CopyIcon />} onClick={onCopy} { ...otherProps }>
          {text}
        </Button>
      }
    </Tooltip>
  )
})


export {
  Component as CopyToClipboard,
  type CopyToClipboardProps
}