import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  Button,
  Text,
} from '@/index'


import Example from '../components/Example'
import ExampleItem from '../components/ExampleItem'

function Lorem(props: { count: number }) {
  const arr = []

  for (let i = 0; i < props.count; i++) {
    arr.push(
      <Text key={i}>
        Sit nulla est ex deserunt exercitation anim occaecat. Nostrud ullamco deserunt aute id consequat veniam incididunt duis in sint irure nisi. Mollit officia cillum Lorem ullamco minim nostrud elit officia tempor esse quis.
        Sunt ad dolore quis aute consequat. Magna exercitation reprehenderit magna aute tempor cupidatat consequat elit dolor adipisicing. Mollit dolor eiusmod sunt ex incididunt cillum quis. Velit duis sit officia eiusmod Lorem aliqua enim laboris do dolor eiusmod. Et mollit incididunt nisi consectetur esse laborum eiusmod pariatur proident Lorem eiusmod et. Culpa deserunt nostrud ad veniam.
      </Text>
    )
  }

  return <>{arr}</>
}


function Usage(props: any) {
  const { count, size = 'md', name='Open Modal' } = props
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <>
      <Button onClick={onOpen}>{name}</Button>

      <Modal isOpen={isOpen} onClose={onClose} size={size} >
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Modal Title</ModalHeader>
          <ModalCloseButton />
          <ModalBody>
            <Lorem count={count} />
          </ModalBody>

          <ModalFooter>
            <Button flex={1} onClick={onClose}>
              Close
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  )
}

export default function() {
  return (
    <Example title="Modal">
      <ExampleItem title="Usage" direction='row'>
        <Usage count={1}></Usage>
      </ExampleItem>

      <ExampleItem title="Overflow" direction='row'>
        <Usage count={10}></Usage>
      </ExampleItem>

      <ExampleItem title="Sizes" direction='row'>
        {['xs', 'sm', 'md', 'lg', 'xl', '2xl', '3xl', '4xl', '5xl', '6xl', 'full'].map((item, index) => 
          <Usage count={10} size={item} name={item} key={index}></Usage>
        )}
      </ExampleItem>
    </Example>
  )
}