import { $color } from "@/theme/utils"
import { PartsStyleFunction, SystemStyleObject } from "@chakra-ui/theme-tools"

const baseStyle: PartsStyleFunction = (props) => {
  return {
    borderColor: $color(props, "scene.border"),
  }
}

const variantSolid: SystemStyleObject = {
  borderStyle: "solid",
}

const variantDashed: SystemStyleObject = {
  borderStyle: "dashed",
}

const variants = {
  solid: variantSolid,
  dashed: variantDashed,
}

const defaultProps = {
  variant: "solid",
}

export const Divider = {
  baseStyle,
  variants,
  defaultProps,
}