import { $color } from "@/theme/utils"
import { breadcrumbAnatomy as parts } from "@chakra-ui/anatomy"
import {
  PartsStyleFunction,
  PartsStyleObject,
  SystemStyleFunction,
} from "@chakra-ui/theme-tools"


const baseStyleSeparator: SystemStyleFunction = (props) => {
  return {
    color: $color(props, 'text.masked'),
  }
}

const baseStyleLink: SystemStyleFunction = (props) => {
  const { colorScheme: c } = props

  return {
    cursor: "pointer",
    _hover: {
      textDecoration: "none",
      color: $color(props, `scene.${c}.normal`),
    },
    _focus: {
      boxShadow: 'none',
    }
  }
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  link: baseStyleLink(props),
  separator: baseStyleSeparator(props),
})

const baseSizes: Record<string, PartsStyleObject<typeof parts>> = {
  md: {
    link: {},
  },
}

const defaultProps = {
  size: 'md',
  colorScheme: '?',
}

export const Breadcrumb = {
  parts: parts.keys,
  baseStyle,
  defaultProps,
  sizes: baseSizes,
}