import { Checkbox, type CheckboxProps } from '@chakra-ui/react'
import { ColorScheme, SizeType } from '@/types'
import React from 'react'

interface Props extends CheckboxProps {
  size?: SizeType,
  colorScheme?: ColorScheme
};

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Checkbox ref={ref} { ...props } />
  )
})


export {
  Component as Checkbox,
  type Props as CheckboxProps,
}