import { getValue } from "@/theme/utils/getValue"
import { StyleFunctionProps, mode } from "@chakra-ui/theme-tools"
import { rgba } from 'polished'
import { cssVar } from '@chakra-ui/react'
import {
  CSS_VAR_PREFIX,
  HOVER_OPACITY,
  TEXT_COLOR_FOR_SOLID_BG_MAP,
} from '@/constants'

export function $cssVar(name: string) {
  return cssVar(`${CSS_VAR_PREFIX}-${name}`).reference
}

export function $color(props: StyleFunctionProps, key: string, opacity = 1) {
  const { theme } = props

  const lightColor = getValue(theme.colors.light, key)
  const darkColor = getValue(theme.colors.dark, key)

  if (opacity === 1) {
    return mode(lightColor, darkColor)(props)
  } else {
    return mode(rgba(lightColor, opacity), rgba(darkColor, opacity))(props)
  }
}

export function $shadow(props: StyleFunctionProps, key: string) {
  const { theme } = props

  const lightShadow = getValue(theme.shadows.light, key)
  const darkShadow = getValue(theme.shadows.dark, key)

  return mode(lightShadow, darkShadow)(props)
}


export function $hover(props: StyleFunctionProps) {
  const { theme } = props

  return $color(props, `scene.${theme.config.defaultColorScheme}.normal`, HOVER_OPACITY)
}

export function $textColorForSolidBg(props: StyleFunctionProps, isActive = false) {
  const { colorScheme: c, theme } = props
  const { normal, active } = theme.colors.scene[c]

  const key = isActive ? active : normal
  const colors = TEXT_COLOR_FOR_SOLID_BG_MAP[key]

  return mode(colors.light, colors.dark)(props)
}

export function rgbaToHex(color: string) {
  if (!color.startsWith('rgb')) {
    return color
  }

  const r: Array<string> = []

  color.replace(/(\d.?\d*)/g, (s) => {
    const n = parseFloat(s)
    const c = n < 1 ? (~~(n * 256)).toString(16) : n.toString(16)
    r.push(`${c.length === 1 ? '0' : ''}${c}`)

    return s
  })

  return `#${r.join('')}`
}