import { NumberInputField, type NumberInputFieldProps } from '@chakra-ui/react'
import React from 'react'


interface Props extends NumberInputFieldProps {}

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <NumberInputField ref={ref} {...props} />
  )
});

(Component as any).id = 'Input'

export {
  Component as NumberInputField,
  type Props as NumberInputFieldProps,
}