import { avatarAnatomy as parts } from "@chakra-ui/anatomy"
import { randomColor } from "@chakra-ui/theme-tools"
import type {
  PartsStyleFunction,
  PartsStyleObject,
  SystemStyleFunction,
} from "@chakra-ui/theme-tools"
import { $color, $cssVar } from "@/theme/utils"

const baseStyleBadge: SystemStyleFunction = (props) => {
  return {
    transform: 'translate(25%, 25%)',
    borderRadius: "full",
    border: "0.2em solid",
    borderColor: $color(props, 'bg.input.normal'),
    bg: 'white',
    boxSize: '1.25em',
  }
}

const baseStyleExcessLabel: SystemStyleFunction = (props) => {
  return {
    bg: $color(props, 'bg.input.normal'),
  }
}

const baseStyleContainer: SystemStyleFunction = (props) => {
  const { name } = props

  const bg = name ? randomColor({ string: name }) : $color(props, 'scene.border')

  return {
    bg,
    color: $color(props, 'text.normal'),
    border: 'none',
    verticalAlign: "top",
  }
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  badge: baseStyleBadge(props),
  excessLabel: baseStyleExcessLabel(props),
  container: baseStyleContainer(props),
})

function getSize(sizeName: string): PartsStyleObject<typeof parts> {
  const size = $cssVar(`sizes-${sizeName}`)

  return {
    container: {
      width: size,
      height: size,
      fontSize: `calc(${size} / 2.5)`,
    },
    excessLabel: {
      width: size,
      height: size,
    },
    label: {
      fontSize: `calc(${size} / 2.5)`,
    },
  }
}

const sizes = {
  xs: getSize('md'),
  sm: getSize('4xl'),
  md: getSize('6xl'),
  lg: getSize('8xl'),
  xl: getSize('10xl'),
  '2xl': getSize('11xl'),
  '3xl': getSize('12xl'),
}

const defaultProps = {
  size: "md",
}

export const Avatar = {
  parts: parts.keys,
  baseStyle,
  sizes,
  defaultProps,
}