import { $cssVar } from "@/theme/utils"
import { cssVar, SystemStyleObject } from "@chakra-ui/theme-tools"


const $size = cssVar("spinner-size")

const baseStyle = () => {
  return {
    width: [$size.reference],
    height: [$size.reference],
  }
}

const sizes: Record<string, SystemStyleObject> = {
  xs: {
    [$size.variable]: $cssVar('fontSizes-md'),
  },
  sm: {
    [$size.variable]: $cssVar('fontSizes-2xl'),
  },
  md: {
    [$size.variable]: $cssVar('fontSizes-5xl'),
  },
  lg: {
    [$size.variable]: $cssVar('fontSizes-10xl'),
  },
}

const defaultProps = {
  size: "md",
  colorScheme: '?'
}

export const Spinner = {
  baseStyle,
  sizes,
  defaultProps,
}