import React from 'react'
import { Box, Center } from '@chakra-ui/react'
import { QRCodeSVG } from 'qrcode.react'
import { ReactComponent as QRCodeBg } from './bg.svg'
import { useColor } from '@/index';


interface QRCodeProps {
  hasFrame?: boolean;
  value: string;
  size?: string | number,
}

function QRCodeFrame() {
  return (
    <Box w="100%" h="100%" position='absolute'>
      <QRCodeBg width='100%' height='100%' />
    </Box>
  )
}

const Component = React.forwardRef((props: QRCodeProps, ref: React.Ref<any>) => {
  const {
    hasFrame, value, size, 
  } = props

  const realWidth = size ?? '6.25rem'
  const realHeight = size ?? '6.25rem'
  const qrCodeSize = hasFrame ? '80%' : '100%'

  const bgColor = useColor('bg.normal')
  const fgColor = useColor('text.normal')

  return (
    <Box
      ref={ref}
      w={realWidth}
      h={realHeight}
      position='relative'
    >
      { hasFrame && <QRCodeFrame /> }
      <Center h='100%'>
        <QRCodeSVG
          value={value}
          size={qrCodeSize as any}
          level="M"
          bgColor={bgColor}
          fgColor={fgColor}
        />
      </Center>
    </Box>
  )
})

export {
  Component as QRCode,
  type QRCodeProps, 
}