import { DISABLED_OPACITY } from "@/constants"
import { $color, $textColorForSolidBg } from "@/theme/utils"
import { checkboxAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  PartsStyleObject,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"

const baseStyleControl: SystemStyleFunction = (props) => {
  const { colorScheme: c } = props

  const colors = {
    inputBg:  $color(props, 'bg.input.normal'),
    borderNormal:  $color(props, 'scene.border'),
    borderError:  $color(props, 'scene.danger.normal'),
    bgNormal:  $color(props, `scene.${c}.normal`),
    bgActive:  $color(props, `scene.${c}.active`),
    textActive: $textColorForSolidBg(props, true),
    textNormal: $textColorForSolidBg(props),
  }

  return {
    w: "100%",
    border: '1px solid',
    borderColor: colors.borderNormal,
    color: colors.textNormal,
    bg: colors.inputBg,
    borderRadius: "0.25em",
    transitionProperty: "background, border",
    transitionDuration: "normal",

    _checked: {
      bg: colors.bgNormal,
      borderColor: 'transparent',

      _hover: {
        bg: colors.bgActive,
        borderColor: '',
        color: colors.textActive,
      },

      _disabled: {
        borderColor: '',
        bg: colors.bgNormal,
        color: '',
        opacity: DISABLED_OPACITY,
      },
    },

    _indeterminate: {
      bg: colors.bgNormal,
      borderColor: 'transparent',
    },

    _disabled: {
      bg: '',
      borderColor: '',
      opacity: DISABLED_OPACITY,
    },

    _focusVisible: {
      boxShadow: "none",
    },

    _invalid: {
      borderWidth: '1px',
      borderColor: colors.borderError,
    },
  }
}

const baseStyleContainer: SystemStyleObject = {
  _disabled: { cursor: "not-allowed" },
}

const baseStyleLabel: SystemStyleObject = {
  userSelect: "none",
  _disabled: {
    opacity: DISABLED_OPACITY
  },
}

const baseStyleIcon: SystemStyleObject = {
  transitionProperty: "transform",
  transitionDuration: "normal",
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  icon: baseStyleIcon,
  container: baseStyleContainer,
  control: baseStyleControl(props),
  label: baseStyleLabel,
})

const sizes: Record<string, PartsStyleObject<typeof parts>> = {
  xs: {
    container: {
      fontSize: "xs",
    },
    control: {
      h: 'md',
      w: 'md',
    },
    label: {
      fontSize: "xs",
    },
    icon: {
      fontSize: '2xs',
    },
  },
  sm: {
    container: {
      fontSize: "sm",
    },
    control: {
      h: '2xl',
      width: '2xl',
    },
    label: {
      fontSize: "sm",
    },
    icon: {
      fontSize: 'xs',
    },
  },
  md: {
    container: {
      fontSize: "sm",
    },
    control: {
      w: '4xl',
      h: '4xl',
    },
    label: {
      fontSize: "sm",
    },
    icon: {
      fontSize: 'md',
    },
  },
  lg: {
    container: {
      fontSize: "sm"
    },
    control: {
      w: '5xl',
      h: '5xl',
    },
    label: {
      fontSize: "sm",
    },
    icon: {
      fontSize: 'xl',
    },
  },
}

const defaultProps = {
  size: "xs",
  colorScheme: '?',
}

export const Checkbox = {
  parts: parts.keys,
  baseStyle,
  sizes,
  defaultProps,
}