import { useLayoutEffect } from "react";
import { useColorMode } from "@/index";


interface CssVarProviderProps {
  children?: React.ReactNode,
  onChangeColorMode: (colorMode: string) => void
}

export default function CssVarUpdater(props: CssVarProviderProps) {
  const { children, onChangeColorMode } = props

  const { colorMode } = useColorMode()

  useLayoutEffect(() => {
    onChangeColorMode(colorMode)
  }, [colorMode])

  return (
    <>
      {children}
    </>
  )
}