import { Spinner, type SpinnerProps } from '@chakra-ui/react' 
import React from 'react'


interface Props extends SpinnerProps {}


const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Spinner ref={ref} speed='0.65s' { ...props } />
  )
})


export {
  Component as Spinner,
  type SpinnerProps,
}