import { DISABLED_OPACITY } from "@/constants"
import { $color } from "@/theme/utils"
import { numberInputAnatomy as parts } from "@chakra-ui/anatomy"
import {
  calc,
  cssVar,
  PartsStyleFunction,
  PartsStyleObject,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"
import { Input } from "./input"

const $stepperWidth = cssVar("number-input-stepper-width")
const $inputPadding = cssVar("number-input-input-padding")
const inputPaddingValue = calc($stepperWidth).add("0.5rem").toString()


const baseStyleRoot: SystemStyleObject = {
  [$stepperWidth.variable]: "1.5rem",
  [$inputPadding.variable]: inputPaddingValue,
}

const baseStyleField: SystemStyleFunction = (props) => {
  return {
    ...Input.baseStyle?.(props).field
  }
}

const baseStyleStepperGroup: SystemStyleObject = {
  width: [$stepperWidth.reference],
}

const baseStyleStepper: SystemStyleFunction = (props) => {
  return {
    borderStart: "1px solid",
    borderStartColor: $color(props, 'bg.input.normal'),
    color: $color(props, 'text.normal'),
    _active: {
      bg: '',
    },
    _disabled: {
      opacity: DISABLED_OPACITY,
      cursor: "not-allowed",
    },
    _last: {
      borderColor: $color(props, 'bg.input.normal'),
    }
  }
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  root: baseStyleRoot,
  field: baseStyleField(props),
  stepperGroup: baseStyleStepperGroup,
  stepper: baseStyleStepper(props),
})

function getSize(size: string): PartsStyleObject<typeof parts> {
  const sizeStyle = Input.sizes[size]

  const fontSizeMap: Record<string, string> = {
    'sm': '2xs',
    'md': '2xs',
    'lg': '2xs',
  }
  const fontSize = fontSizeMap[size]

  return {
    field: {
      ...sizeStyle.field,
      verticalAlign: "top",
      paddingInlineEnd: $inputPadding.reference,
    },
    stepper: {
      fontSize,
      _first: {

      },
      _last: {
        mt: "-1px",
        borderTopWidth: 1,
      },
    },
  }
}

const sizes = {
  sm: getSize("sm"),
  md: getSize("md"),
  lg: getSize("lg"),
}

const variantFlushed: PartsStyleFunction<typeof parts> = (props) => {
  return {
    ...Input.variants.flushed(props),
    stepper: {
      _first: {
        border: 'none',
      },
      _last: {
        border: 'none',
      }
    }
  }
}

const variants = {
  ...Input.variants,
  flushed: variantFlushed,
}

const defaultProps = {
  ...Input.defaultProps,
}

export const NumberInput = {
  parts: parts.keys,
  baseStyle,
  sizes,
  variants,
  defaultProps,
}