import {
  Button,
  useDisclosure,
  AlertDialog,
  AlertDialogOverlay,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogBody,
  AlertDialogCloseButton,
  AlertDialogFooter,
} from '@/index'
import React from 'react'


import Example from '../components/Example'
import ExampleItem from '../components/ExampleItem'


function TransitionExample(props: any) {
  const { isOpen, onOpen, onClose } = useDisclosure()

  return (
    <>
      <Button onClick={onOpen}>{props.name}</Button>
      <AlertDialog onClose={onClose} isOpen={isOpen} size={props.size} >
        <AlertDialogOverlay />
        <AlertDialogContent>
          <AlertDialogHeader>Discard Changes?</AlertDialogHeader>
          <AlertDialogCloseButton />
          <AlertDialogBody>
            Are you sure you want to discard all of your notes? 44 words will be
            deleted.
          </AlertDialogBody>
          <AlertDialogFooter>
            <Button flex="1" onClick={onClose}>
              OK
            </Button>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </>
  )
}


export default function() {
  return (
    <Example title="AlertDialog">
      <ExampleItem title="Usage" direction='row'>
        <TransitionExample name='Open' />
      </ExampleItem>

      <ExampleItem title="Sizes" direction='row'>
        <TransitionExample name='xs' size="xs" />
        <TransitionExample name='sm' size="sm" />
        <TransitionExample name='md' size="md" />
        <TransitionExample name='lg' size="lg" />
        <TransitionExample name='xl' size="xl" />
        <TransitionExample name='2xl' size="2xl" />
        <TransitionExample name='3xl' size="3xl" />
        <TransitionExample name='4xl' size="4xl" />
        <TransitionExample name='5xl' size="5xl" />
        <TransitionExample name='6xl' size="6xl" />
        <TransitionExample name='full' size="full" />
      </ExampleItem>
    </Example>
  )
}