import { StyleFunctionProps } from "@chakra-ui/theme-tools"
import type {
  SystemStyleObject,
  SystemStyleFunction,
} from "@chakra-ui/theme-tools"
import { $color, $textColorForSolidBg } from "@/theme/utils"
import { sizesOptions } from "../common/sizeOptions"
import { DISABLED_OPACITY } from "@/constants"


const baseStyle: SystemStyleFunction = () => {
  return {
    lineHeight: "1.2",
    borderRadius: "md",
    fontWeight: "600",
    transitionProperty: "common",
    transitionDuration: "normal",
    cursor: 'pointer',
    _focusVisible: {
      boxShadow: "none",
    },
    _disabled: {
      opacity: DISABLED_OPACITY,
      cursor: "not-allowed",
      boxShadow: "none",
    },
    _hover: {
      _disabled: {
        bg: "",
      },
    },
  }
}

function getColors(props: StyleFunctionProps) {
  const { colorScheme: c } = props

  return {
    normal: $color(props, `scene.${c}.normal`),
    active: $color(props, `scene.${c}.active`),
    currColor: $color(props, `text.normal`),
  }
}

const variantGhost: SystemStyleFunction = (props) => {
  const {
    active,
    currColor,
  } = getColors(props)

  const hoverColor = $textColorForSolidBg(props, true)

  return {
    color: currColor,
    bg: "transparent",
    _hover: {
      bg: active,
      color: hoverColor,
    },
    _active: {
      bg: '',
    },
  }
}

const variantOutline: SystemStyleFunction = (props) => {
  const {
    normal,
    active,
  } = getColors(props)

  return {
    border: "1px solid",
    borderColor: normal,
    color: normal,
    _hover: {
      borderColor: active,
      color: active,
    },
    _focus: {
      boxShadow: ''
    }
  }
}


const variantSolid: SystemStyleFunction = (props) => {
  const {
    normal,
    active,
  } = getColors(props)
  
  const color = $textColorForSolidBg(props)
  const hoverColor = $textColorForSolidBg(props, true)

  return {
    bg: normal,
    color,
    _hover: {
      bg: active,
      color: hoverColor,
      _disabled: {
        bg: normal,
      },
    },
    _active: {
      bg: '',
    },
  }
}

const variantLink: SystemStyleFunction = (props) => {
  const {
    normal,
    active,
  } = getColors(props)

  return {
    padding: 0,
    height: "auto",
    lineHeight: "normal",
    verticalAlign: "baseline",
    color: normal,
    _hover: {
      textDecoration: "underline",
      color: active,
      _disabled: {
        textDecoration: "none",
      },
    },
    _active: {
      color: '',
    },
  }
}


const variantUnstyled: SystemStyleObject = {
  bg: "none",
  color: "inherit",
  display: "inline",
  lineHeight: "inherit",
  m: 0,
  p: 0,
}

const variants = {
  ghost: variantGhost,
  outline: variantOutline,
  solid: variantSolid,
  link: variantLink,
  unstyled: variantUnstyled,
}

const sizes: Record<string, SystemStyleObject> = {
  ...sizesOptions,
}

const defaultProps = {
  variant: "solid",
  size: "md",
  colorScheme: '?',
}

export const Button = {
  baseStyle,
  variants,
  sizes,
  defaultProps,
}