import { progressAnatomy as parts } from "@chakra-ui/anatomy"
import {
  generateStripe,
  mode,
  PartsStyleFunction,
  PartsStyleObject,
  StyleFunctionProps,
} from "@chakra-ui/theme-tools"
import type {
  SystemStyleObject,
  SystemStyleFunction,
} from "@chakra-ui/theme-tools"
import { $color } from "@/theme/utils"


function filledStyle(props: StyleFunctionProps): SystemStyleObject {
  const { colorScheme: c, isIndeterminate, hasStripe } = props

  const stripeStyle = mode(
    generateStripe(),
    generateStripe("1rem", "rgba(0,0,0,0.1)"),
  )(props)

  const bgColor = $color(props, `scene.${c}.normal`)

  const gradient = `linear-gradient(
    to right,
    transparent 0%,
    ${bgColor} 50%,
    transparent 100%
  )`

  const addStripe = !isIndeterminate && hasStripe

  return {
    ...(addStripe && stripeStyle),
    ...(isIndeterminate ? { bgImage: gradient } : { bgColor }),
  }
}

const baseStyleLabel: SystemStyleFunction = (props) => {
  return {
    lineHeight: "1",
    fontSize: "0.25em",
    fontWeight: "bold",
    color: $color(props, 'text.normal'),
  }
}

const baseStyleTrack: SystemStyleFunction = (props) => {
  return {
    bg: $color(props, 'bg.input.normal'),
    borderRadius: '4xs',
  }
}

const baseStyleFilledTrack: SystemStyleFunction = (props) => {
  return {
    transitionProperty: "common",
    transitionDuration: "slow",
    ...filledStyle(props),
  }
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  label: baseStyleLabel(props),
  filledTrack: baseStyleFilledTrack(props),
  track: baseStyleTrack(props),
})

const sizes: Record<string, PartsStyleObject<typeof parts>> = {
  xs: {
    track: {
      h: '3xs',
    },
  },
  sm: {
    track: {
      h: '2xs',
    },
  },
  md: {
    track: {
      h: 'xs',
    },
  },
  lg: {
    track: {
      h: "md",
    },
  },
}

const defaultProps = {
  size: "sm",
  colorScheme: '?',
}

export const Progress = {
  parts: parts.keys,
  sizes,
  baseStyle,
  defaultProps,
}