import { $color, $shadow } from "@/theme/utils"
import { popoverAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"
import { cssVar } from "@chakra-ui/theme-tools"

const $popperBg = cssVar("popper-bg")
const $arrowBg = cssVar("popper-arrow-bg")
const $arrowShadowColor = cssVar("popper-arrow-shadow-color")
const $popperArrowSize = cssVar('popper-arrow-size')

const baseStylePopper: SystemStyleObject = {
  zIndex: 10,
}

const baseStyleContent: SystemStyleFunction = (props) => {
  const shadowColor = $color(props, 'scene.border')

  return {
    [$popperArrowSize.variable]: '0.875rem',
    [$popperBg.variable]: $color(props, 'bg.tooltip.normal'),
    [$arrowBg.variable]: $popperBg.reference,
    [$arrowShadowColor.variable]: shadowColor,
    bg: $popperBg.reference,
    width: "container.md",
    border: "1px solid",
    borderColor: $color(props, 'scene.border'),
    borderRadius: "2xs",
    boxShadow: $shadow(props, 'normal'),
    zIndex: "inherit",
    _focusVisible: {
      outline: 0,
      boxShadow: '',
    },
    _focus: {
      boxShadow: '',
    }
  }
}

const baseStyleHeader: SystemStyleFunction = (props) => {
  return {
    px: 'xs',
    py: '2xs',
    borderBottomWidth: "1px",
    fontWeight: 'bold',
    borderBottomColor: $color(props, 'scene.border'),
  }
}

const baseStyleBody: SystemStyleObject = {
  px: 'xs',
  py: '2xs',
}

const baseStyleFooter: SystemStyleObject = {
  px: 'xs',
  py: '2xs',
  borderTopWidth: "1px",
}

const baseStyleCloseButton: SystemStyleFunction = (props) =>{
  return {
    position: "absolute",
    top: '2xs',
    right: 'xs',
  }
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  popper: baseStylePopper,
  content: baseStyleContent(props),
  header: baseStyleHeader(props),
  body: baseStyleBody,
  footer: baseStyleFooter,
  arrow: {},
  closeButton: baseStyleCloseButton(props),
})

export const Popover = {
  parts: parts.keys,
  baseStyle,
}