import { ColorScheme } from '@/types'
import React from 'react'
import { Breadcrumb, type BreadcrumbProps } from '@chakra-ui/react'

interface Props extends BreadcrumbProps {
  colorScheme?: ColorScheme,
};

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Breadcrumb ref={ref} { ...props } />
  )
})


export {
  Component as Breadcrumb,
  type Props as BreadcrumbProps,
}