import { editableAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"
import { Input } from "./input"

const baseStylePreview: SystemStyleObject = {
  py: '4xs',
  fontSize: 'sm',
  transitionProperty: "common",
  transitionDuration: "normal",
  border: '1px solid transparent',
}

const baseStyleInput: SystemStyleFunction = (props) => {
  return {
    ...Input.baseStyle(props).field,
    ...Input.variants.outline(props).field,
    borderRadius: '0.333333em',
    fontSize: 'sm',
    py: '4xs',
  }
}

const baseStyleTextarea: SystemStyleFunction = (props) => {
  return {
    ...baseStyleInput(props),
    h: 'auto',
  }
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => {
  return {
    preview: baseStylePreview,
    input: baseStyleInput(props),
    textarea: baseStyleTextarea(props),
  }
}

const defaultProps = {
  colorScheme: '?',
}

export const Editable = {
  parts: parts.keys,
  baseStyle,
  defaultProps,
}