import { $hover, $color } from "@/theme/utils"
import { listAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"


const baseStyleContainer: SystemStyleObject = {
  '> li:not(style)~*:not(style)': {
    mt: 0,
  }
}

const baseStyleIcon: SystemStyleObject = {
  marginEnd: "0.5rem",
  display: "inline",
  verticalAlign: "text-bottom",
  flexShrink: '0',
}

const baseStyleItem: SystemStyleFunction = (props) => {
  return {
    display: 'flex',
    alignItems: 'center',
    borderBottom: '1px solid',
    borderColor: $color(props, 'scene.border'),
    px: 'md',
    py: 0,
    minHeight: '7xl',
    _focus: {
      boxShadow: "none",
    },
    _hover: {
      bg: $hover(props),
    },
    _last: {
      borderBottom: 'none',
    }
  }
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => {
  return {
    container: baseStyleContainer,
    item: baseStyleItem(props),
    icon: baseStyleIcon,
  }
}

export const List = {
  parts: parts.keys,
  baseStyle,
}