import SwitchColorMode from "../SwitchColorMode"
import { Box, Heading, List, ListItem, useColorMode } from '@/index'
import { useEffect, useState } from "react"


export default function Navigation(props: any) {
  const [categories, setCategories] = useState<any>([])

  useEffect(() => {
    const list = Array.from(document.querySelectorAll('.category'))
    setCategories(list)
  }, [])

  const { colorMode, toggleColorMode } = useColorMode()

  return (
    <Box position='fixed' p="24px" w={props.width}>
      <SwitchColorMode onChange={toggleColorMode}>
        {colorMode}
      </SwitchColorMode>

      <List>
        {categories.map((item: any, index: number) => (
          <ListItem key={index} as="a" href={`#${item.id}`}>
            <Heading size='sm'>{item.id}</Heading>
          </ListItem>)
        )}
      </List>
    </Box>
  )
}