import { $color, $shadow, rgbaToHex } from "@/theme/utils"
import { cssVar, mode, SystemStyleFunction } from "@chakra-ui/theme-tools"

const $bg = cssVar("tooltip-bg")
const $arrowBg = cssVar("popper-arrow-bg")
const $arrowShadowColor = cssVar("popper-arrow-shadow-color")

const baseStyle: SystemStyleFunction = (props) => {
  const bg = $color(props, 'bg.tooltip.normal')

  const opacity = mode(0.05, 0.5)(props)
  const shadowColor = rgbaToHex($color(props, 'bg.tooltip.outer', opacity))

  return {
    [$bg.variable]: bg,
    [$arrowBg.variable]: bg,
    [$arrowShadowColor.variable]: shadowColor,
    px: "0.666667em",
    py: "0.333333em",
    bg: [$bg.reference],
    color: $color(props, 'text.normal'),
    borderRadius: "0.333333em",
    fontSize: "sm",
    boxShadow: $shadow(props, 'normal'),
    maxW: "container.md",
    zIndex: "tooltip",
  }
}

export const Tooltip = {
  baseStyle,
}
