import {
  Stack,
  FormControl,
  FormLabel,
  Switch,
  SimpleGrid,
} from '@/index'


import Example from '../components/Example'
import ExampleItem from '../components/ExampleItem'

export default function() {
  return (
    <Example title="Switch">
      <ExampleItem title="Usage" direction='row'>
        <FormControl display='flex' alignItems='center'>
          <FormLabel htmlFor='email-alerts' mb='0'>
            Enable email alerts?
          </FormLabel>
          <Switch id='email-alerts' />
        </FormControl>
      </ExampleItem>

      <ExampleItem title="Sizes">
        <Stack align='center' direction='row'>
          <Switch size='xs' />
          <Switch size='sm' />
          <Switch size='md' />
          <Switch size='lg' />
        </Stack>
      </ExampleItem>

      <ExampleItem title="State depending behavior">
        <FormControl as={SimpleGrid} columns={4}>
          <FormLabel htmlFor='isChecked'>isChecked:</FormLabel>
          <Switch id='isChecked' isChecked />

          <FormLabel htmlFor='isDisabled'>isDisabled:</FormLabel>
          <Switch id='isDisabled' isDisabled defaultChecked />

          <FormLabel htmlFor='isFocusable'>isFocusable:</FormLabel>
          <Switch id='isFocusable' isFocusable isDisabled />

          <FormLabel htmlFor='isInvalid'>isInvalid:</FormLabel>
          <Switch id='isInvalid' isInvalid />

          <FormLabel htmlFor='isReadOnly'>isReadOnly:</FormLabel>
          <Switch id='isReadOnly' isReadOnly />

          <FormLabel htmlFor='isRequired'>isRequired:</FormLabel>
          <Switch id='isRequired' isRequired />
        </FormControl>
      </ExampleItem>

      <ExampleItem title="Colors" direction='row'>
        <Switch colorScheme='primary' isChecked />
        <Switch colorScheme='secondary' isChecked />
        <Switch colorScheme='success' isChecked />
        <Switch colorScheme='danger' isChecked />
        <Switch colorScheme='warning' isChecked />
        <Switch colorScheme='purple' isChecked />
      </ExampleItem>
    </Example>
  )
}