import { Stack, Heading, Box, type StackProps } from '@/index'


export interface ExampleItemProps extends StackProps {
  title: string,
}


export default function ExampleItem(props: ExampleItemProps) {
  const { title, ...otherProps } = props  

  return (
    <Box py="12px">
      <Heading fontSize="18px" pb="12px">{title}</Heading>
      <Stack {...otherProps} />
    </Box>
  )
}