import { DISABLED_OPACITY } from "@/constants"
import { $color } from "@/theme/utils"
import { tabsAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  PartsStyleInterpolation,
  PartsStyleObject,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"
import { sizesOptions } from "@/theme/common/sizeOptions"


const baseStyleRoot: SystemStyleFunction = () => {
  return {
    fontSize: 0,
  }
}

const baseStyleTab: SystemStyleObject = {
  _focus: {
    boxShadow: "none",
  },
}

const baseStyleTablist: SystemStyleObject = {
  fontWeight: 600,
  px: 0,
}

const baseStyleTabpanel: SystemStyleObject = {

}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  root: baseStyleRoot(props),
  tab: baseStyleTab,
  tablist: baseStyleTablist,
  tabpanel: baseStyleTabpanel,
})

const baseSizes: Record<string, PartsStyleObject<typeof parts>> = {
  xs: {
    tablist: {
      ...sizesOptions.xs,
      px: 0,
    },
    tab: {
      minW: 'md',
      px: sizesOptions.xs.px,
    },
    tabpanel: {
      p: '3xs',
      fontSize: sizesOptions.xs.fontSize,
    }
  },
  sm: {
    tablist: {
      ...sizesOptions.sm,
      px: 0,
    },
    tab: {
      minW: '2xl',
      px: sizesOptions.sm.px,
    },
    tabpanel: {
      p: '3xs',
      fontSize: sizesOptions.sm.fontSize,
    }
  },
  md: {
    tablist: {
      ...sizesOptions.md,
      px: 0,
    },
    tab: {
      minW: '5xl',
      px: sizesOptions.md.px,
    },
    tabpanel: {
      p: '2xs',
      fontSize: sizesOptions.md.fontSize,
    }
  },
  lg: {
    tablist: {
      ...sizesOptions.lg,
      px: 0,
    },
    tab: {
      minW: '10xl',
      px: sizesOptions.lg.px,
    },
    tabpanel: {
      p: '2xs',
      fontSize: sizesOptions.lg.fontSize,
    }
  },
}

const getCommonStyles = (isFitted: boolean) => {
  return {
    fontSize: 'inherit',
    fontWeight: "inherit",
    borderRadius: 'inherit',
    _active: {
      bg: '',
    },
    _disabled: {
      opacity: DISABLED_OPACITY,
      cursor: "not-allowed",
    },
    flex: isFitted ? '1 1 0' : 'inherit'
  }
}

const variantLine: PartsStyleFunction<typeof parts> = (props) => {
  const { isFitted, colorScheme: c  } = props

  return {
    tablist: {
      borderBottomWidth: '1px',
      borderRadius: 'none',
    },
    tab: {
      color: $color(props, 'text.masked'),
      _selected: {
        color: $color(props, `scene.${c}.normal`),
        borderColor: "currentColor",
      },
      ...getCommonStyles(isFitted),
    },
  }
}

const variantRounded: PartsStyleFunction<typeof parts> = (props) => {
  const { theme, isFitted, colorScheme: c } = props

  let selectedColor = theme.colors.dark.text.normal
  if (['success', 'warning'].includes(c)) {
    selectedColor = theme.colors.light.text.normal
  }

  return {
    tablist: {
      bg: $color(props, 'bg.input.normal'),
      display: isFitted ? 'flex' : 'inline-flex',
    },
    tab: {
      color: $color(props, 'text.normal'),
      _selected: {
        color: selectedColor,
        bg: $color(props, `scene.${c}.normal`),
      },
      ...getCommonStyles(isFitted),
    },
  }
}


const variants: Record<string, PartsStyleInterpolation<typeof parts>> = {
  "line": variantLine,
  "rounded": variantRounded,
}


const defaultProps = {
  size: "xs",
  variant: "rounded",
  colorScheme: '?',
}

export const Tabs = {
  parts: parts.keys,
  baseStyle,
  sizes: baseSizes,
  defaultProps,
  variants,
}