import {
  Flex,
  Center,
  Square,
  Circle,
} from '@/index'

import Example from '../components/Example'
import ExampleItem from '../components/ExampleItem'

import { theme } from '@/theme'

type Dict = Record<string, any>

function getColors(source: Dict, prefix = '') {

  const colorsMap: Dict = {}

  const createColorMap = (data: any, path: string) => {
    Object.entries(data).forEach(([key, value]) => {
      const currPath = `${path}${path ? '-' : ''}${key}`
      if (typeof value === 'object') {
        createColorMap(value, currPath)
      } else {
        colorsMap[currPath] = value
      }
    })
  }

  createColorMap(source, prefix)

  return colorsMap
}



export default function() {
  const ligthColors = getColors(theme.colors.light, '')
  const lightShadows = getColors(theme.shadows.light, '')

  const darkColors = getColors(theme.colors.dark, '')
  const darkShadows = getColors(theme.shadows.dark, '')
  

  return (
    <Example title="Theme Colors">
      <ExampleItem title="Colors" spacing='0px'>
        {Object.entries(ligthColors).map(([key, value], index) => {
          return (
            <Flex key={index}>
              <Flex w="150px" alignItems='center'>{key}</Flex>
              <Center h='60px' w="60px">
                <Circle size='50px' bg={value}></Circle>
              </Center>
              <Center h='60px' w="60px">
                <Circle size='50px' bg={darkColors[key]}></Circle>
              </Center>
            </Flex>
          )
        })}
      </ExampleItem>

      <ExampleItem title="Shadows" spacing='0px'>
        {Object.entries(lightShadows).map(([key, value], index) => {
          return (
            <Flex key={index}>
              <Flex w="150px" alignItems='center'>{key}</Flex>
              <Center h='60px' w="60px">
                <Circle size='50px' boxShadow={value}></Circle>
              </Center>
              <Center h='60px' w="60px">
                <Circle size='50px' boxShadow={darkShadows[key]}></Circle>
              </Center>
            </Flex>
          )
        })}
      </ExampleItem>
    </Example>
  )
}