import { tableAnatomy as parts } from "@chakra-ui/anatomy"

import type {
  PartsStyleFunction,
  PartsStyleObject,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"
import { $color } from "../utils"

const baseStyle: PartsStyleObject<typeof parts> = {
  table: {
    fontVariantNumeric: "lining-nums tabular-nums",
    borderCollapse: "collapse",
    width: "100%",
  },
  th: {
    fontFamily: "heading",
    fontWeight: "bold",
    textTransform: "uppercase",
    letterSpacing: "wider",
    textAlign: "start",
  },
  td: {
    textAlign: "start",
  },
  caption: {
    mt: 'md',
    fontFamily: "heading",
    textAlign: "center",
    fontWeight: "medium",
  },
}

const numericStyles: SystemStyleObject = {
  "&[data-is-numeric=true]": {
    textAlign: "end",
  },
}

const variantSimple: PartsStyleFunction<typeof parts> = (props) => {
  return {
    th: {
      color: $color(props, 'text.normal'),
      borderBottom: "1px solid",
      borderColor: $color(props, 'scene.border'),
      ...numericStyles,
    },
    td: {
      borderBottom: "1px solid",
      borderColor: $color(props, 'scene.border'),
      ...numericStyles,
    },
    caption: {
      color: $color(props, 'text.normal'),
    },
    tfoot: {
      tr: {
        "&:last-of-type": {
          th: { borderBottomWidth: 0 },
        },
      },
    },
  }
}

const variantStripe: PartsStyleFunction<typeof parts> = (props) => {
  return {
    th: {
      color: $color(props, 'text.normal'),
      borderBottom: "1px solid",
      borderColor: $color(props, 'scene.border'),
      ...numericStyles,
    },
    td: {
      borderBottom: "1px solid",
      borderColor: $color(props, 'scene.border'),
      ...numericStyles,
    },
    caption: {
      color: $color(props, 'text.normal'),
    },
    tbody: {
      tr: {
        "&:nth-of-type(odd)": {
          "th, td": {
            borderBottomWidth: "1px",
            borderColor: $color(props, 'scene.border'),
          },
          td: {
            background: $color(props, 'bg.input.normal'),
          },
        },
      },
    },
    tfoot: {
      tr: {
        "&:last-of-type": {
          th: { borderBottomWidth: 0 },
        },
      },
    },
  }
}

const variants = {
  simple: variantSimple,
  striped: variantStripe,
  unstyled: {},
}

const sizes: Record<string, PartsStyleObject<typeof parts>> = {
  sm: {
    th: {
      px: "md",
      py: "3xs",
      lineHeight: "md",
      fontSize: "xs",
    },
    td: {
      px: "md",
      py: "2xs",
      fontSize: "sm",
      lineHeight: "md",
    },
    caption: {
      px: "md",
      py: "2xs",
      fontSize: "xs",
    },
  },
  md: {
    th: {
      px: "2xl",
      py: "xs",
      lineHeight: "md",
      fontSize: "xs",
    },
    td: {
      px: "2xl",
      py: "md",
      lineHeight: "xl",
    },
    caption: {
      px: "2xl",
      py: "2xs",
      fontSize: "sm",
    },
  },
  lg: {
    th: {
      px: "4xl",
      py: "md",
      lineHeight: "xl",
      fontSize: "sm",
    },
    td: {
      px: "4xl",
      py: "xl",
      lineHeight: "2xl",
    },
    caption: {
      px: "2xl",
      py: "2xs",
      fontSize: "md",
    },
  },
}

const defaultProps = {
  variant: "simple",
  size: "md",
  colorScheme: "?",
}

export const Table = {
  parts: parts.keys,
  baseStyle,
  variants,
  sizes,
  defaultProps,
}