import { $color, $textColorForSolidBg } from "@/theme/utils"
import {
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"

const baseStyle: SystemStyleObject = {
  borderRadius: '1em',
  px: '0.666667em',
  py: '0.333333em',
  fontSize: 'xs',
  textTransform: 'none',
  fontWeight: 'normal',
  lineHeight: 1,
  display: 'inline-flex',
  alignItems: 'center',
  minH: '2xl',
  border: '1px solid transparent'
}


const variantOutline: SystemStyleFunction = (props) => {
  const { colorScheme: c } = props
  const color = $color(props, `scene.${c}.normal`)

  return {
    color,
    borderColor: color,
  }
}

const variantSolid: SystemStyleFunction = (props) => {
  const { colorScheme: c } = props
  const color = $textColorForSolidBg(props)

  return {
    bg: $color(props, `scene.${c}.normal`),
    color,
  }
}

const variantSubtle: SystemStyleFunction = (props) => {
  const { colorScheme: c } = props
  const bgColor = $color(props, `scene.${c}.normal`, 0.1)
  const color = $color(props, `scene.${c}.normal`)

  return {
    bg: bgColor,
    color,
  }
}


const variants = {
  "outline": variantOutline,
  "solid": variantSolid,
  "subtle": variantSubtle,
}


const defaultProps = {
  variant: "subtle",
  colorScheme: '?',
}

export const Badge = {
  baseStyle,
  defaultProps,
  variants,
}