import { AvatarSizeType } from '@/types'
import { Avatar, type AvatarProps } from '@chakra-ui/react'
import React from 'react'

interface Props extends AvatarProps {
  size?: AvatarSizeType,
};

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Avatar ref={ref} { ...props } />
  )
})


export {
  Component as Avatar,
  type Props as AvatarProps,
}