import { ColorScheme } from '@/types'
import { Code, type CodeProps } from '@chakra-ui/react'
import React from 'react'


interface Props extends CodeProps {
  colorScheme?: ColorScheme,
}

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Code ref={ref} { ...props } />
  )
})

export {
  Component as Code,
  type Props as CodeProps,
}
