import { Tooltip, type TooltipProps } from '@chakra-ui/react'
import React from "react";

interface Props extends TooltipProps {}

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Tooltip ref={ref} hasArrow={true} { ...props } />
  )
})


export {
  Component as Tooltip,
  type Props as TooltipProps,
}