import { DISABLED_OPACITY } from "@/constants"
import { $hover, $color } from "@/theme/utils"
import { accordionAnatomy as parts } from "@chakra-ui/anatomy"
import {
  PartsStyleFunction,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"

const baseStyleRoot: SystemStyleObject = {
  mt: 0,
  fontSize: 'md',
  width: '100%',
}

const baseStyleContainer: SystemStyleObject = {
  borderTop: 'none',
  borderBottom: 'none',
}

const baseStyleButton: SystemStyleFunction = (props) => {
  return {
    outline: 'none',
    borderBottom: '1px solid',
    borderColor: $color(props, 'scene.border'),
    px: 'md',
    py: 0,
    minHeight: '7xl',
    _focus: {
      boxShadow: "none",
    },
    _hover: {
      bg: $hover(props),
    },
    _disabled: {
      opacity: DISABLED_OPACITY,
      cursor: 'not-allowed',
    }
  }
}

const baseStylePanel: SystemStyleObject = {
  fontSize: 'xs',
  p: 'md',
}

const baseStyleIcon: SystemStyleObject = {
  fontSize: 'xl',
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => {
  return {
    root: baseStyleRoot,
    container: baseStyleContainer,
    button: baseStyleButton(props),
    panel: baseStylePanel,
    icon: baseStyleIcon,
  }
}

export const Accordion = {
  parts: parts.keys,
  baseStyle,
}