import { useToast } from "@chakra-ui/react";
import { useEffect } from "react";
import { createToast, ToastHelper } from "../createToast";


let toast: ToastHelper

interface ToastProviderProps {
  children?: React.ReactNode,
}

const Component = (props: ToastProviderProps) => {
  const toastProto = useToast()
  
  useEffect(() => {
    toast = createToast(toastProto)
  }, [toastProto])

  return (
    <>
      {props.children}
    </>
  )
}

export {
  Component as ToastProvider,
  toast,
}