import { DISABLED_OPACITY } from "@/constants"
import { $color } from "@/theme/utils"
import { inputAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  PartsStyleObject,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"
import { sizesOptions } from "../common/sizeOptions"

const baseStyle: PartsStyleFunction<typeof parts> = (props) => {
  const { colorScheme: c = 'primary' } = props

  return {
    field: {
      outline: 'none',
      border: 'none',
      width: "100%",
      minWidth: 0,
      position: "relative",
      appearance: "none",
      bg: 'transparent',
      transitionProperty: "common",
      transitionDuration: "normal",
      _readOnly: {
        userSelect: "all",
        borderColor: 'transparent !important',
      },
      _placeholder: {
        color: $color(props, 'text.placeholder'),
      },
      _disabled: {
        opacity: DISABLED_OPACITY,
        cursor: "not-allowed",
      },
      _focus: {
        borderColor: $color(props, `scene.${c}.active`),
      },
      _invalid: {
        borderColor: $color(props, 'scene.danger.normal'),
      },
    },
  }
}

const size: Record<string, SystemStyleObject> = {
  ...sizesOptions,
}

const sizes: Record<string, PartsStyleObject<typeof parts>> = {
  lg: {
    field: size.lg,
    addon: {
      ...size.lg,
      h: 'auto',
    },
  },
  md: {
    field: size.md,
    addon: {
      ...size.md,
      h: 'auto',
    }
  },
  sm: {
    field: size.sm,
    addon: {
      ...size.sm,
      h: 'auto',
    }
  },
  xs: {
    field: size.xs,
    addon: {
      ...size.xs,
      h: 'auto',
    }
  },
}

const variantOutline: PartsStyleFunction<typeof parts> = (props) => {
  return {
    field: {
      border: '1px solid',
      borderColor: $color(props, 'bg.input.normal'),
    },
    addon: {
      border: 'none',
    },
  }
}

const variantFilled: PartsStyleFunction<typeof parts> = (props) => {
  const { field, addon } = variantOutline(props)

  return {
    field: {
      ...field,
      bg: $color(props, 'bg.input.normal'),
    },
    addon: {
      ...addon,
    },
  }
}

const variantFlushed: PartsStyleFunction<typeof parts> = (props) => {
  return {
    field: {
      borderBottom: "1px solid",
      borderColor: $color(props, 'bg.input.normal'),
      borderRadius: 0,
      px: 0,
    },
  }
}

const variantUnstyled: PartsStyleObject<typeof parts> = {
  field: {
    bg: "transparent",
    px: 0,
    height: "auto",
  },
  addon: {
    bg: "transparent",
    px: 0,
    height: "auto",
  },
}

const variants = {
  outline: variantOutline,
  filled: variantFilled,
  flushed: variantFlushed,
  unstyled: variantUnstyled,
}

const defaultProps = {
  size: "md",
  variant: "filled",
  colorScheme: '?',
}

export const Input = {
  parts: parts.keys,
  baseStyle,
  sizes,
  variants,
  defaultProps,
}