import { DISABLED_OPACITY } from "@/constants"
import { $color, $cssVar } from "@/theme/utils"
import { switchAnatomy as parts } from "@chakra-ui/anatomy"
import type {
  PartsStyleFunction,
  PartsStyleObject,
  SystemStyleFunction,
  SystemStyleObject,
} from "@chakra-ui/theme-tools"
import { calc, cssVar } from "@chakra-ui/theme-tools"

const $width = cssVar("switch-track-width")
const $height = cssVar("switch-track-height")

const $diff = cssVar("switch-track-diff")
const diffValue = calc.subtract($width, $height)

const $translateX = cssVar("switch-thumb-x")

const baseStyleTrack: SystemStyleFunction = (props) => {
  const { colorScheme: c } = props

  return {
    borderRadius: "full",
    p: "2px",
    width: [$width.reference],
    height: [$height.reference],
    transitionProperty: "common",
    transitionDuration: "fast",
    bg: $color(props, 'bg.input.normal'),
    _focusVisible: {
      boxShadow: "none",
    },
    _disabled: {
      opacity: DISABLED_OPACITY,
      cursor: "not-allowed",
    },
    _checked: {
      bg: $color(props, `scene.${c}.normal`),
      _hover: {
        bg: $color(props, `scene.${c}.active`),
      },
    },
    _invalid: {
      boxShadow: `inset 0 0 0 1px ${$color(props, 'scene.danger.normal')}`
    },
  }
}

const baseStyleThumb: SystemStyleObject = {
  bg: "white",
  transitionProperty: "transform",
  transitionDuration: "normal",
  borderRadius: "inherit",
  width: [$height.reference],
  height: [$height.reference],
  _checked: {
    transform: `translateX(${$translateX.reference})`,
  },
}

const baseStyle: PartsStyleFunction<typeof parts> = (props) => ({
  container: {
    [$diff.variable]: diffValue,
    [$translateX.variable]: $diff.reference,
    _rtl: {
      [$translateX.variable]: calc($diff).negate().toString(),
    },
  },
  track: baseStyleTrack(props),
  thumb: baseStyleThumb,
})

const sizes: Record<string, PartsStyleObject<typeof parts>> = {
  xs: {
    container: {
      [$width.variable]: $cssVar('sizes-3xl'),
      [$height.variable]: $cssVar('sizes-md'),
    },
  },
  sm: {
    container: {
      [$width.variable]: $cssVar('sizes-5xl'),
      [$height.variable]: $cssVar('sizes-2xl'),
    },
  },
  md: {
    container: {
      [$width.variable]: $cssVar('sizes-7xl'),
      [$height.variable]: $cssVar('sizes-4xl'),
    },
  },
  lg: {
    container: {
      [$width.variable]: $cssVar('sizes-9xl'),
      [$height.variable]: $cssVar('sizes-5xl'),
    },
  },
}

const defaultProps = {
  size: "xs",
  colorScheme: '?',
}

export const Switch = {
  parts: parts.keys,
  baseStyle,
  sizes,
  defaultProps,
}