import {
  Box,
  CloseButton,
  Heading,
  Text,
  Flex,
  useColor,
  useShadow,
  Center,
  useReverseColor,
} from "@/index"
import { TickCircleSolidIcon, WarningCircleSolidIcon, CrossCircleSolidIcon } from '@node-real/bee-icons'
import { UseToastOptions } from "@chakra-ui/react"
import { useMemo } from "react"

const iconMap = {
  'success': TickCircleSolidIcon,
  'warning': WarningCircleSolidIcon,
  'danger': CrossCircleSolidIcon,
}

interface ToastProps extends Omit<UseToastOptions, 'status' | 'variant'> {
  status?: 'success' | 'warning' | 'danger',
  icon?: React.ReactNode,
  onClose?: () => void,
  size?: 'sm' | 'md'
}

const Component = (props: ToastProps) => {
  const {
    title,
    status = 'warning',
    isClosable = true,
    description,
    icon,
    onClose,
  } = props

  const bgColor = useColor('bg.tooltip.normal')
  const boxShadow = useShadow('normal')
  const iconColor = useColor(`scene.${status}.normal`)
  const leftBgColor = useReverseColor('bg.normal')

  const iconComponent = useMemo(() => {
    if (icon) {
      return icon
    }

    const SvgIcon = iconMap[status]

    return <SvgIcon color={iconColor} w='4xl' h='4xl' />
  }, [icon, status])

  return (
    <Flex
      bgColor={bgColor}
      boxShadow={boxShadow}
      minH='7xl'
      borderRadius='3xs'
      overflow='hidden'
    >
      <Center
        w='3.75rem'
        flexShrink={0}
        bg={leftBgColor}
      >
        {iconComponent}
      </Center>
      <Flex
        p='sm'
        direction='column'
        justifyContent='center'
        flex='1'
        w={280}
      >
        {title && <Heading size='md'>{title}</Heading>}
        {description && <Text fontSize='sm'>{description}</Text>}
      </Flex>

      {isClosable && (
        <Box
          py='sm'
          pr='sm'
          flexShrink={0}
        >
          <CloseButton
            onClick={onClose}
          />
        </Box>
      )}
    </Flex>
  )
}

export {
  Component as Toast,
  type ToastProps
}