import { useColorMode, useTheme } from "./index"
import { $color } from "@/theme/utils"

export function useReverseColor(key: string, opacity: number = 1) {
  const { colorMode } = useColorMode()
  const theme = useTheme()

  const props: any = {
    theme,
    colorMode: colorMode === 'light' ? 'dark' : 'light',
  }

  return $color(props, key, opacity)
}
