
import { ColorScheme } from '@/types'
import { useTheme } from '@/index'
import { CircularProgress, type CircularProgressProps } from '@chakra-ui/react'
import React from 'react'

interface Props extends CircularProgressProps {
  colorScheme?: ColorScheme,
}

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  const theme = useTheme()

  const { colorScheme = theme.config.defaultColorScheme, ...otherProps } = props
  const color = `scene.${colorScheme}.normal`

  return (
    <CircularProgress color={color} { ...otherProps } />
  )
})


export {
  Component as CircularProgress,
  type CircularProgressProps,
}