import { Editable, type EditableProps } from '@chakra-ui/react'
import { ColorScheme } from '@/types'
import React from 'react'

interface Props extends EditableProps {
  colorScheme?: ColorScheme,
};

const Component = React.forwardRef((props: Props, ref: React.Ref<any>) => {
  return (
    <Editable ref={ref} { ...props } />
  )
})


export {
  Component as Editable,
  type Props as EditableProps,
}
